.HSuper {
  margin: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-image: url("../../Images/bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.HSuper img {
  height: 50px;
  cursor: pointer;
  transition: 0.5s;
}

.HSuper img:hover {
  scale: 1.1;
}

.HBtn {
  height: 60px;
  width: 150px;
  border: solid white 3px;
}
