.ASuper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: #709b49;
}

.Acontainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 60px;
  width: 80%;
}

.Acontainer img {
  height: 400px;
  border: solid black 7px;
}

.AImg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48%;
}

.AText {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48%;
  flex-direction: column;
}

.AText h1 {
  font-size: 48px;
}

.AText p {
  font-size: 28px;
  text-align: center;
}
