.NBSuper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 90px;
  width: 100%;
  background-color: #61bee1;
}

.NBContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 80%;
}

.NBSocials {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 440px;

  /* border: solid red 1px; */
}

.NBContainer img {
  height: 70px;
}

.NBSocials img {
  height: 50px;
  cursor: pointer;
}

